<template>
  <div>
    <div style="background-color: aqua"></div>
    <el-button type="primary" @click="getFatherData"
      >FatherData+1</el-button
    >
    <el-button type="primary" @click="getFatherDatao"
      >FatherData0</el-button
    >

    <!--  -->
    <!-- v-model  :  -->
    <!--    this.$emit -->
    <Son :FatherData="FatherData" @changeData="changeData"></Son>

    <div style="background-color: aqua">DOM</div>
    <div class="Father" id="FatherDiv" ref="FatherDiv">
      <el-button @click="pushDiv">DIV</el-button>
      <div>DIV</div>
    </div>

    <div style="background-color: aqua">js</div>
    <div>
      <el-button @click="createNewObj"></el-button>
      <div>{{ createObj }}</div>
    </div>

    <div style="background-color: aqua">window.open</div>
    <div>
      <el-input
        v-model="canshuName"
        placeholder=""
        clearable
      ></el-input>
      <el-input
        v-model="canshuValue"
        placeholder=""
        clearable
      ></el-input>
      <el-button @click="openNew">window.open</el-button>
    </div>
  </div>
</template>

<script>
import Son from "./Son.vue"; //
export default {
  data() {
    return {
      FatherData: 1,
      createObj: null,
      canshuName: "",
      canshuValue: "",
    };
  },
  components: {
    Son, //
  },
  methods: {
    getFatherData() {
      this.FatherData++;
    },
    getFatherDatao() {
      this.FatherData = 0;
    },
    changeData(data) {
      this.FatherData = data;
    },

    //DON
    pushDiv() {
      let oldDiv = document.getElementById("FatherDiv"); //
      let newDiv = document.createElement("div"); //div
      newDiv.innerText = "div"; //div
      //   this.$refs.FatherDiv.appendChild(newDiv); //vueref
      oldDiv.appendChild(newDiv); //
    },
    //
    createNewObj() {
      let newobj = new Object(); //new
      newobj.name = "zm";
      newobj.year = "18";
      this.createObj = newobj;
    },
    //window.open
    openNew() {
      window.open(
        "http://localhost:6300/#/newOpen?" +
          this.canshuName +
          "=" +
          this.canshuValue
      );
    },
  },
};
</script>

<style lang="less" scoped>
.Father {
  background-color: rgb(90, 90, 247);
  min-height: 50px;
}
</style>