<template>
  <div>window.open {{ urlQuery }}</div>
</template>

<script>
export default {
  data() {
    return {
      urlQuery: null,
    };
  },

  created() {
    var str = location.href; //
    var num = str.indexOf("?");
    str = str.substr(num + 1); //
    this.urlQuery = str;
  },
};
</script>